  .description{
    z-index: 10;
    height: 90px;
    background-color:#ffffff;
    position: relative;
    top: -40px;
    width: 90%;
    margin: auto;
    border-radius: 10px;
    padding: 15px;
    -webkit-box-shadow: 0 4px 8px -6px gray;
         -moz-box-shadow: 0 4px 8px -6px gray;
              box-shadow: 0 4px 8px -6px gray;
  }
  
  
  .description-title{
    font-family: 'Quicksand', sans-serif;
    font-weight: lighter;
  }
  
  .sub-info{
    font-family: 'Open sans', sans-serif;
    font-size: 0.75em;
  }
  
  
  .icon-easy{
    color: #ffd972;
  }
  
  .icon-cake{
    color: #c7eae4;
  
  }
  
  .icon-clock{
    color: #efa7a7;
  }
  
  .text-muted{
    font-size: 0.7em;
  }
  
  
  