@import url(https://fonts.googleapis.com/css?family=Open+Sans|Quicksand:500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header-title{
  font-family: 'Quicksand', sans-serif;
  font-size: 1.15em;
}

.navbar-brand{
  font-family: 'Quicksand', sans-serif;
}

.logo{
  color: #ffd972;
}


.card-img{
    border-radius: 15px;
    height: 210px;
  }
  
  
  .img1, .img2, .img3{
    background-color: #ffd972;
  }
  
  /*.img2{
    background-color: #efa7a7;
  }
  
  
  .img3{
    background-color: #c7eae4;
  
  }*/
  
  
  
  .description{
    z-index: 10;
    height: 90px;
    background-color:#ffffff;
    position: relative;
    top: -40px;
    width: 90%;
    margin: auto;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 4px 8px -6px gray;
  }
  
  
  .description-title{
    font-family: 'Quicksand', sans-serif;
    font-weight: lighter;
  }
  
  .sub-info{
    font-family: 'Open sans', sans-serif;
    font-size: 0.75em;
  }
  
  
  .icon-easy{
    color: #ffd972;
  }
  
  .icon-cake{
    color: #c7eae4;
  
  }
  
  .icon-clock{
    color: #efa7a7;
  }
  
  .text-muted{
    font-size: 0.7em;
  }
  
  
  
.recipeImg{
    border-radius: 10px;
    background-color: #fcbcb8;
    width: 100px;
    height: 100px;
}

.card-sub-info{
    font-family: 'Open sans', sans-serif;
    font-size: 0.6em;
}

.info{
    padding-left:120px;
    border-radius: 10px;
    box-shadow: 4px 4px 10px -9px gray;
}
